}

#content-docs {
  overflow-x: auto;
  white-space: nowrap;
  padding: 0;
  margin: 0;
  position: relative;
  width: 100%;
  box-sizing: border-box;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrolling-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: max-content;
  padding: 0;
  margin: 0;
}

.button-docs {
  display: inline-block;
  padding: 10px 20px;
  border: 1px solid #e5e5e5;
  color: #000;
  text-decoration: none;
  border-radius: 5%;
  margin: 0 5px;
  text-align: center;
  font-size: 14px;
  background-color: #fff;
  transition: background-color 0.3s ease;
  flex-shrink: 0;
  min-width: 150px;
  max-width: 150px;
  box-sizing: border-box;
}

.button-docs:hover {
  background-color: #f0f0f0;
  text-decoration: none;
  color: #000;
}

.button-docs.clicked {
  background-color: #f0f0f0;
}

