    color: #333; /* Match text color with other elements */
    background-color: transparent; /* No background */
    border-radius: 10px; /* Match border-radius with other elements */
    white-space: nowrap; /* Prevents the label from wrapping */
  }

  .news-title h3 {
    padding-right: 0px;
  }

  .news-item {
    flex-direction: column;
    height: 100%;
  }

  .news-text {
    padding: 15px;
  }
}

@media (max-width: 380px) {
  .language-switcher {
    flex-direction: column;
    top: 10px;
    right: 10px;
    left: auto;
  }

  .btn {
    margin-bottom: 2px;
  }

  #content-docs a {
    flex-basis: 100%;
    font-size: 12px;
  }
}
